import React, {useEffect} from "react";
import Typing from 'react-typing-animation';

//CSS
import "./home.css";

let titulo;
let letras = "Qualidade e preço bom é aqui!".split("");

const HomeComponent = () => {

    const escrever = () => {
        console.log(letras)
        letras.forEach((letra, index) => {
            setTimeout(() => titulo.innerHTML += letra, 75*index);
        });
    }

    return (
        <div id="home" className="banner">
            <div className="container-wd home">
                <Typing hideCursor={true}>
                    <h1 className="titulo texto-branco">Qualidade e preço bom é aqui!</h1>
                </Typing>
                <button className="texto-bold texto-branco"><i class="fas fa-angle-down"></i> Ver produtos</button>
            </div>
        </div>
    )
}

export default HomeComponent;